<template>
  <ec-drawer :title="title" :visible="visible" size="large" @close="onclose" class="ec-draw-form">
    <ec-container height="auto" fixed="top bottom" color-code="007">
      <ec-container-body>
        <ec-panel color-code="002">
          <ec-panel-header>
            <ec-panel-item-left class="ec-panel-item-left">
              <div class="ec-panel-title">
                <div class="ec-bar bg-color-1"></div>
                <div class="ec-title" style="color: rgb(24, 144, 255); font-weight: 700">
                  
                </div>
              </div>
            </ec-panel-item-left>

            <ec-panel-item-right>
              <div style="display: inline-block">
                <span style="margin-left: 14px"
                  >{{ nowYear }};{{ weightUnitName }}{{
                    areaUnitName
                  }}</span
                >
              </div>
            </ec-panel-item-right>
          </ec-panel-header>
          <ec-panel-item>
            <ec-form ref="ruleForm1" :model="formData" :rules="rules">
              <a-row type="flex" :gutter="12">
                <a-col :span="8">
                  <a-form-model-item label="">{{
                    formData.leafTypeName || '--'
                  }}</a-form-model-item>
                </a-col>
                <a-col :span="8">
                  <a-form-model-item label="">{{
                    formData.orgName || '--'
                  }}</a-form-model-item>
                </a-col>
                <a-col :span="8">
                  <a-form-model-item label="">{{
                    formData.oprTypeName || '--'
                  }}</a-form-model-item>
                </a-col>
                <a-col :span="12">
                  <a-form-model-item label="">{{
                    formData.remark || '--'
                  }}</a-form-model-item>
                </a-col>
              </a-row>
            </ec-form>
          </ec-panel-item>
        </ec-panel>

        <ec-panel color-code="002">
          <ec-panel-header>
            <ec-panel-item-left class="ec-panel-item-left">
              <div class="ec-panel-title">
                <div class="ec-bar bg-color-1"></div>
                <div class="ec-title" style="color: rgb(24, 144, 255); font-weight: 700">
                  /
                </div>
              </div>
            </ec-panel-item-left>
          </ec-panel-header>

          <ec-panel-body>
            <ec-table
              border
              show-overflow
              keep-source
              ref="xTable"
              class="my-xtable-antd"
              :data="tableData"
            >
              <ec-table-col field="name" title="" align="left" min-width="150" />
              <ec-table-colgroup title="">
                <ec-table-col field="planTotalWeight" title="" align="center" width="200" />
                <ec-table-col field="planInnerWeight" title="" align="center" width="200" />
                <ec-table-col
                  field="planExportWeight"
                  title=""
                  align="center"
                  width="200"
                />
              </ec-table-colgroup>
              <ec-table-col field="planPlantArea" title="" align="right" width="200" />
            </ec-table>
          </ec-panel-body>
        </ec-panel>
      </ec-container-body>
      <ec-container-footer fixed>
        <a-space>
          <a-button @click="onclose"></a-button>
        </a-space>
      </ec-container-footer>
    </ec-container>
  </ec-drawer>
</template>
<script>
export default {
  name: 'ExecutionEnterAdjustDetManage',
  data() {
    return {
      title: '',
      tableData: [],
      data: {},
      leafTypeName: '',
      leafTypeCode: '',
      weightUnitName: '',
      areaUnitName: '',
      nowYear: '',
      rowData: '',
      orgCode: '',
      visible: false,
      isShowSaveAndAdd: true,
      ruleForm: {},
      leafTypeList: [],
      workOptType: [
        { id: '0', name: '' },
        { id: '1', name: '' },
      ],
      formData: {
        leafTypeCode: '',
        orgName: '',
        oprType: '',
        remark: '',
      },
      beforeAjustData: {},
      rules: {
        oprType: { required: true, message: '', trigger: 'blur' },
        orgName: { required: true, message: '', trigger: 'blur' },
        leafTypeCode: { required: true, message: '', trigger: 'blur' },
      },
    };
  },

  methods: {
    // 
    handleGetBeforeAdjustData() {
      // 
      const beforeAjustData = {
        name: '',
        planTotalWeight: 500000,
        planInnerWeight: 200000,
        planExportWeight: 300000,
        planPlantArea: 600000,
      };
      //  
      this.beforeAjustData = this.handleDataParse(beforeAjustData);
    },
    //  ()
    handleDataParse(beforeAjustData) {
      const obj = beforeAjustData;
      obj.planTotalWeight = (obj.planTotalWeight / this.weightUnit).toFixed(2);
      obj.planInnerWeight = (obj.planInnerWeight / this.weightUnit).toFixed(2);
      obj.planExportWeight = (obj.planExportWeight / this.weightUnit).toFixed(2);
      obj.planPlantArea = (obj.planPlantArea / this.areaUnit).toFixed(2);
      return obj;
    },

    // 
    handleCurrentDataChange() {
      this.tableData.splice(this.tableData.length - 1, 1);
      this.handleAfterAdjustData();
    },

    // 
    show(row) {
      this.visible = true;

      this.data = row;
      const selectRow = row.selectedRows;

      this.leafTypeName = row.leafTypeObj.leafTypeName;
      this.weightUnitName = row.weightUnitObj.weightUnitName;
      this.weightUnit = row.weightUnitObj.weightUnitCd;
      this.areaUnitName = row.areaUnitObj.areaUnitName;
      this.areaUnit = row.areaUnitObj.areaUnitCd;
      this.nowYear = row.nowYear;

      this.formData = selectRow;
      const leafTypeObj = this.leafTypeList.find(
        (item) => item.leafTypeCode === selectRow.leafTypeCode,
      );
      this.formData.leafTypeName = leafTypeObj?.leafTypeName;

      const oprTypeObj = this.workOptType.find((item) => item.id === selectRow.oprType);
      this.formData.oprTypeName = oprTypeObj?.name;

      // 
      this.handleGetBeforeAdjustData();
      this.tableData.push(this.beforeAjustData);

      // 
      const currentAdjustData = {
        name: '',
        planTotalWeight: selectRow.planTotalWeight || 0,
        planInnerWeight: selectRow.planInnerWeight || 0,
        planExportWeight: selectRow.planExportWeight || 0,
        planPlantArea: selectRow.planPlantArea || 0,
      };
      this.tableData.push(currentAdjustData);

      // 

      this.handleAfterAdjustData();
    },
    sumNum(list, field) {
      let count = 0;
      list.forEach((item) => {
        const value = item;
        value[field] = value[field] || 0;
        count += Number(item[field]);
      });
      return count.toFixed(2);
    },
    handleAfterAdjustData() {
      const columns = ['planTotalWeight', 'planInnerWeight', 'planExportWeight', 'planPlantArea'];
      const afterAdjustData = {
        name: '',
      };
      columns.forEach((item) => {
        afterAdjustData[item] = this.sumNum(this.tableData, item);
      });
      this.tableData.push(afterAdjustData);
    },

    // 
    handleGetLeafTypeList() {
      this.$apis.tbm.tabaccoType.query({}).then((res) => {
        this.leafTypeList = res?.rows || [];
        const { leafTypeList } = this;
        if (leafTypeList && leafTypeList.length > 0) {
          this.leafTypeCd = (
            leafTypeList.find((item) => item.leafTypeName === '') || {}
          ).leafTypeCode;
          console.log(`0000000000000000000=${this.formData.leafTypeCode}`);
          this.formData.leafTypeCode = this.leafTypeCd;
        }
      });
    },

    // 
    onclose() {
      this.$refs.ruleForm1.resetFields();
      this.tableData = [];
      this.visible = false;
    },
  },
};
</script>
