<template>
  <ec-container height="100%">
    <ec-container-header>
      <ec-panel-item>
        <ec-panel-item-left>
          <ec-header title="" icon="onetomany" color-code="008" />
        </ec-panel-item-left>
      </ec-panel-item>
    </ec-container-header>
    <ec-container-body>
      <ec-panel :auto="false">
        <ec-panel-toolbar>
          <ec-query-panel :defaultFold="true" @query="$refs.table.pageAction(1)" @reset="onReset">
            <ec-query-item label="">
              <ec-select
                showSearch
                allowClear
                placeholder=""
                v-model="queryItems.leafTypeCode"
              >
                <a-select-option v-for="leafTypeEk in leafTypeList" :key="leafTypeEk.leafTypeCode">
                  {{ leafTypeEk.leafTypeName }}
                </a-select-option>
              </ec-select>
            </ec-query-item>

            <ec-query-item label="">
              <a-select allow-clear placeholder="" v-model="queryItems.submitState">
                <a-select-option v-for="item in workSubmitState" :key="item.id">
                  {{ item.name }}
                </a-select-option>
              </a-select>
            </ec-query-item>

            <!-- <ec-query-item label="">
              <a-select allow-clear placeholder="" v-model="queryItems.auditState">
                <a-select-option v-for="item in workAuditState" :key="item.id">
                  {{ item.name }}
                </a-select-option>
              </a-select>
            </ec-query-item> -->
            <ec-query-item label="">
              <a-select allow-clear placeholder="" v-model="queryItems.oprType">
                <a-select-option v-for="item in workOptType" :key="item.id">
                  {{ item.name }}
                </a-select-option>
              </a-select>
            </ec-query-item>
            <template #inner>
              <ec-query-item label="">
                <a-input v-model="queryItems.remark" placeholder="" />
              </ec-query-item>

              <ec-query-item label="">
                <org-picker v-model="queryItems.orgCode" placeholder="" />
              </ec-query-item>
            </template>
          </ec-query-panel>
          <ec-divider />
        </ec-panel-toolbar>
        <ec-panel-header>
          <ec-panel-item>
            <ec-panel-item-left>
              <a-button type="primary" @click="onAdd"></a-button>
              <a-button @click="onDel"></a-button>
              <a-button @click="onSubmit"></a-button>
            </ec-panel-item-left>
            <ec-panel-item-right>
              <a-space :size="15">
                <span
                  ><ec-select
                    showSearch
                    allowClear
                    placeholder=""
                    v-model="weightUnit"
                    @select="handleUnitParse()"
                    style="width: 150px"
                  >
                    <a-select-option
                      v-for="weightUnit in weightUnitList"
                      :key="weightUnit.weightUnitCd"
                    >
                      {{ weightUnit.weightUnitName }}
                    </a-select-option>
                  </ec-select>
                </span>
                <span
                  ><ec-select
                    showSearch
                    allowClear
                    placeholder=""
                    v-model="areaUnit"
                    @select="handleUnitParse()"
                    style="width: 150px"
                  >
                    <a-select-option v-for="areaUnit in areaUnitList" :key="areaUnit.areaUnitCd">
                      {{ areaUnit.areaUnitName }}
                    </a-select-option>
                  </ec-select>
                </span>
              </a-space>
            </ec-panel-item-right>
          </ec-panel-item>
        </ec-panel-header>
        <ec-panel-body>
          <ec-table
            ref="table"
            border
            height="100%"
            :auto="false"
            @page-action="handlePagination"
            :pagination="pagination"
            :data="tableData.rows"
            @checkbox-all="selectAllEvent"
            @checkbox-change="selectRowChange"
            :loading="loading"
            show-footer
            :footer-method="footerMethod"
            highlight-current-row
          >
            <ec-table-col type="checkbox" width="50" align="center"></ec-table-col>
            <ec-table-col title="" type="seq" width="70" align="center" />
            <ec-table-col field="orgName" title="" align="left" min-width="200" />
            <ec-table-colgroup title="">
              <ec-table-col field="aaPlanTotalWeight" title="" align="center" width="120" />
              <ec-table-col field="aaPlanInnerWeight" title="" align="center" width="120" />
              <ec-table-col
                field="aaPlanExportWeight"
                title=""
                align="center"
                width="120"
              />
            </ec-table-colgroup>
            <ec-table-col field="aaPlanPlantArea" title="" align="right" width="120" />
            <ec-table-col field="remark" title="" align="left" width="150" />
            <ec-table-col field="submitState" title="" align="center" width="100">
              <template #default="{ row }">
                <div v-if="row.submitState === '1'">
                  <span></span>
                </div>
                <div v-else>
                  <span></span>
                </div>
              </template>
            </ec-table-col>

            <ec-table-col title="" align="center" width="150">
              <template #default="{ row }">
                <a-button type="link" @click="onEdit(row)"></a-button>
                <a-button type="link" @click="onDetail(row)"></a-button>
              </template>
            </ec-table-col>
          </ec-table>
        </ec-panel-body>
      </ec-panel>
      <execution-enter-adjust-form
        @onQuery="onQuery"
        ref="ExecutionEnterAdjustForm"
      ></execution-enter-adjust-form>
      <execution-enter-adjust-add
        @onQuery="onQuery"
        ref="ExecutionEnterAdjustAdd"
      ></execution-enter-adjust-add>
      <detail ref="Detail"></detail>
    </ec-container-body>
  </ec-container>
</template>

<script>
import ExecutionEnterAdjustForm from './ExecutionEnterAdjustForm.vue';
import ExecutionEnterAdjustAdd from './ExecutionEnterAdjustAdd.vue';
import Detail from './Detail.vue';
// import _ from 'lodash';
import { OrgPicker } from '@euler-material/ecm-ui';

export default {
  name: 'ExecutionEnterAdjustManage',
  components: {
    ExecutionEnterAdjustForm,
    ExecutionEnterAdjustAdd,
    Detail,
    OrgPicker,
  },

  data() {
    return {
      queryItems: {
        busiYear: '',
        leafTypeCode: '10',
        submitState: '',
        auditState: '',
        oprType: '',
        remark: '',
        orgCode: undefined,
      },
      orgCodeList: [],
      weightUnit: 500000,
      areaUnit: 10000,
      nowYear: '',
      loading: false,
      leafTypeCd: '',
      bizCode: '',
      leafTypeList: [
        { leafTypeCode: '10', leafTypeName: '' },
        { leafTypeCode: '20', leafTypeName: '()' },
        { leafTypeCode: '2013', leafTypeName: '()' },
      ],
      oldDataRow: [],
      workSubmitState: [
        { id: '', name: '' },
        { id: '0', name: '' },
        { id: '1', name: '' },
      ],
      workAuditState: [
        { id: '', name: '' },
        { id: '0', name: '' },
        { id: '1', name: '' },
        { id: '2', name: '' },
        { id: '3', name: '' },
      ],
      workOptType: [
        { id: '', name: '' },
        { id: '0', name: '' },
        { id: '1', name: '' },
      ],
      weightUnitList: [
        { weightUnitCd: 1, weightUnitName: '', isSelected: false },
        { weightUnitCd: 50, weightUnitName: '', isSelected: false },
        { weightUnitCd: 500000, weightUnitName: '', isSelected: false },
      ],
      areaUnitList: [
        { areaUnitCd: 10000, areaUnitName: '' },
        { areaUnitCd: 1, areaUnitName: '' },
      ],
      pagination: {
        pageNumber: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: {
        // 
        // totalRows: 0,
        rows: [],
      },
    };
  },
  computed: {
    appConfig() {
      return this.$store.getters.getAppConfig;
    },
  },
  created() {
    const { appCode } = this.$router.history.current.query;
    this.appCode = appCode || 'CM'; // 
  },

  methods: {
    // 
    handleUnitParse() {
      const newTableData = [];
      const handleOlDataRow = JSON.parse(JSON.stringify(this.oldDataRow)) || [];

      handleOlDataRow.forEach((item) => {
        const obj = item;
        obj.aaPlanTotalWeight = (obj.aaPlanTotalWeight / this.weightUnit).toFixed(2);
        obj.aaPlanInnerWeight = (obj.aaPlanInnerWeight / this.weightUnit).toFixed(2);
        obj.aaPlanExportWeight = (obj.aaPlanExportWeight / this.weightUnit).toFixed(2);
        obj.aaPlanPlantArea = (obj.aaPlanPlantArea / this.areaUnit).toFixed(2);
        newTableData.push(obj);
      });
      console.log(newTableData);
      this.tableData.rows = newTableData;
    },

    async handlePagination(pageNumber, pageSize) {
      this.pagination.pageNumber = pageNumber;
      this.pagination.pageSize = pageSize;
      await this.handleGetLeafTypeList();
    },

    // 
    async handleGetLeafTypeList() {
      if (!this.leafTypeList?.length) {
        const res = await this.$apis.bpm.executionEnter.queryLeafType({});
        this.leafTypeList = res?.rows || [];
        this.leafTypeList.some((item) => {
          if (item.leafTypeName === '') {
            this.leafTypeCd = item.leafTypeCode;
            this.queryItems.leafTypeCode = item.leafTypeCode;
            return true;
          } else {
            return false;
          }
        });
      }
      if (!this.orgCode) {
        const orgObj = await this.$apis.bim.curOrg.get({});
        this.bizCode = orgObj?.bizCode;
        this.orgCode = orgObj?.orgUniqueCode; // todo 
      }
      if (!this.queryItems.busiYear > 0) {
        // 
        const data = {
          bizCode: this.appConfig.bizCode, // todo 
          appCode: this.appCode || 'CM',
          yearTypeCode: 'BIZ_YEAR',
        };
        const busiYearObj = await this.$apis.bpm.executionEnter.getBuYear(data);
        this.queryItems.busiYear = busiYearObj?.yearValue;
        this.nowYear = busiYearObj?.yearValue;
      }
      this.onQuery();
    },

    // 
    onQuery() {
      this.loading = true;
      // const { orgCodeList } = this;
      // const orgCode = orgCodeList.length > 0 ? orgCodeList[0] : '';
      const data = {
        pageNo: this.pagination.pageNumber,
        pageSize: this.pagination.pageSize,
        leafTypeCode: this.queryItems.leafTypeCode,
        submitState: this.queryItems.submitState,
        busiYear: this.queryItems.busiYear,
        auditState: this.queryItems.auditState,
        oprType: this.queryItems.oprType,
        remark: this.queryItems.remark,
        orgCode: this.queryItems.orgCode,
      };
      console.log(data);
      this.$apis.bpm.executionEnter
        .queryAdjust(data)
        .then((res) => {
          this.tableData = res;
          const { rows } = this.tableData;
          this.pagination.total = res.totalRows;
          this.oldDataRow = JSON.parse(JSON.stringify(rows));
          console.log(`66666${JSON.stringify(this.oldDataRow)}`);
          // 
          this.handleUnitParse();
        })
        .catch((err) => {
          console.log(err);
        })
        .finally(() => {
          this.loading = false;
          this.selectedRows = [];
        });
    },
    // 
    onAdd() {
      const leafTypeObj = this.leafTypeList.find(
        (res) => res.leafTypeCode === this.queryItems.leafTypeCode,
      );
      const weightUnitObj = this.weightUnitList.find((res) => res.weightUnitCd === this.weightUnit);
      const areaUnitObj = this.areaUnitList.find((res) => res.areaUnitCd === this.areaUnit);
      const selectedRows = {}; // 
      const { leafTypeList } = this;
      const data = {
        type: 'edit',
        leafTypeObj,
        leafTypeList,
        weightUnitObj,
        areaUnitObj,
        selectedRows,
        nowYear: this.queryItems.busiYear,
      };

      this.$refs.ExecutionEnterAdjustAdd.show(data);
    },

    // 
    onEdit(row) {
      const leafTypeObj = this.leafTypeList.find(
        (res) => res.leafTypeCode === this.queryItems.leafTypeCode,
      );
      const weightUnitObj = this.weightUnitList.find((res) => res.weightUnitCd === this.weightUnit);
      const areaUnitObj = this.areaUnitList.find((res) => res.areaUnitCd === this.areaUnit);
      const selectedRows = JSON.parse(JSON.stringify(row)); // 
      const { leafTypeList } = this;
      const data = {
        type: 'edit',
        leafTypeObj,
        leafTypeList,
        weightUnitObj,
        areaUnitObj,
        selectedRows,
        nowYear: this.queryItems.busiYear,
      };

      this.$refs.ExecutionEnterAdjustForm.show(data);
    },

    // 
    onDetail(row) {
      const leafTypeObj = this.leafTypeList.find(
        (res) => res.leafTypeCode === this.queryItems.leafTypeCode,
      );
      const weightUnitObj = this.weightUnitList.find((res) => res.weightUnitCd === this.weightUnit);
      const areaUnitObj = this.areaUnitList.find((res) => res.areaUnitCd === this.areaUnit);
      const selectedRows = JSON.parse(JSON.stringify(row)); // 

      const leafTypeDataSource = this.leafTypeList;
      const data = {
        leafTypeObj,
        leafTypeDataSource,
        weightUnitObj,
        areaUnitObj,
        selectedRows,
        nowYear: this.queryItems.busiYear,
      };

      this.$refs.Detail.show(data);
    },

    // 
    onDel() {
      if (!this.selectedRows || this.selectedRows.length <= 0) {
        this.$message.error('');
        return;
      }

      const submitData = this.selectedRows.find(
        (item) => item.submitState === '1' && item.auditState !== '0',
      );
      console.log(`--${JSON.stringify(submitData)}`);
      if (submitData) {
        this.$message.error('');
        return;
      }
      console.log(JSON.stringify(this.selectedRows));
      this.$confirm({
        title: '?',
        content: () => (
          <div style="color:red;">
            ''''
          </div>
        ),
        onOk: () => {
          this.loading = true;
          const idList = [];
          for (let i = 0; i < this.selectedRows.length; i++) {
            idList.push(this.selectedRows[i].oprRecordId);
          }
          this.$apis.bpm.executionEnter.delete({ oprRecordIds: idList }).then((res) => {
            console.log(JSON.stringify(res));
            if (res.result) {
              this.loading = false;
              this.onQuery();
              this.$message.success('');
            } else {
              this.$message.error('');
            }
          });
        },
      });
    },

    // 
    onSubmit() {
      if (!this.selectedRows || this.selectedRows.length <= 0) {
        this.$message.error('');
        return;
      }

      const submitData = this.selectedRows.find(
        (item) => item.submitState === '1' && item.auditState !== '0',
      );
      console.log(`--${JSON.stringify(submitData)}`);
      if (submitData) {
        this.$message.error('');
        return;
      }
      console.log(JSON.stringify(this.selectedRows));
      this.$confirm({
        title: '?',
        content: () => (
          <div style="color:red;">
            ''''
          </div>
        ),
        onOk: () => {
          this.loading = true;
          const idList = [];
          for (let i = 0; i < this.selectedRows.length; i++) {
            idList.push(this.selectedRows[i].oprRecordId);
          }
          this.$apis.bpm.executionEnter.submit({ oprRecordIds: idList }).then((res) => {
            console.log(JSON.stringify(res));
            if (res.result) {
              this.loading = false;
              this.$message.success('');
              this.onQuery();
            } else {
              this.$message.error('');
            }
          });
        },
      });
    },

    // 
    onReset() {
      this.queryItems.leafTypeCode = this.leafTypeCd;

      this.queryItems.busiYear = this.nowYear;

      this.queryItems.submitState = '';
      this.queryItems.auditState = '';
      this.queryItems.oprType = '';
      this.queryItems.remark = '';
      this.queryItems.orgName = '';
      this.queryItems.orgCode = undefined;

      this.$refs.table.pageAction(1);
    },

    // 
    handleData() {
      const leafTypeObj = this.leafTypeList.find(
        (res) => res.leafTypeCode === this.queryItems.leafTypeCode,
      );
      const weightUnitObj = this.weightUnitList.find((res) => res.weightUnitCd === this.weightUnit);

      const areaUnitObj = this.areaUnitList.find((res) => res.areaUnitCd === this.areaUnit);

      const data = {
        leafTypeObj,
        weightUnitObj,
        areaUnitObj,
        nowYear: this.queryItems.busiYear,
      };
      return data;
    },

    // 
    handleLeafTypeName(typeCd) {
      const leafType = this.leafTypeList.find((item) => item.leafTypeCode === typeCd);
      return leafType?.leafTypeName;
    },

    // 
    sumNum(list, field) {
      let count = 0;
      list.forEach((item) => {
        count += Number(item[field]);
      });
      return count.toFixed(2);
    },
    // 
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 0) {
            return '';
          }
          if (
            ['planTotalWeight', 'planInnerWeight', 'planExportWeight', 'planPlantArea'].includes(
              column.property,
            )
          ) {
            return this.sumNum(data, column.property);
          }
          return null;
        }),
      ];
    },

    // 
    selectAllEvent({ checked, records }) {
      console.log(`${checked}-records----${JSON.stringify(records)}`);
      this.selectedRows = records;
    },

    // 
    selectRowChange({ checked, records }) {
      console.log(
        `${checked}-records----${JSON.stringify(records)}`,
      );
      this.selectedRows = records;
    },
  },
};
</script>
<style lang="less" scoped>
.year {
  font-size: 18px;
  line-height: 22px;

  color: #18191a;
  font-weight: 700;
  padding-top: 18px;
  padding-right: 50px;
  font-family: ' Bold', '', 'Microsoft YaHei', sans-serif;
}
.paddingTop {
  padding-top: 15px;
}
</style>
