<template>
  <ec-drawer
    :title="title"
    :visible="visible"
    size="large"
    @close="onclose"
    class="ec-draw-form"
    :is-confirm="true"
  >
    <ec-container height="auto" fixed="top bottom" color-code="007">
      <ec-container-body>
        <ec-panel color-code="002">
          <ec-panel-header>
            <ec-panel-item-left class="ec-panel-item-left">
              <div class="ec-panel-title">
                <div class="ec-bar bg-color-1"></div>
                <div class="ec-title" style="color: rgb(24, 144, 255); font-weight: 700">
                  
                </div>
              </div>
            </ec-panel-item-left>

            <ec-panel-item-right>
              <div style="display: inline-block">
                <span style="margin-left: 14px"
                  >{{ nowYear }};{{ weightUnitName }}{{
                    areaUnitName
                  }}</span
                >
              </div>
            </ec-panel-item-right>
          </ec-panel-header>
          <ec-panel-item>
            <ec-form ref="ruleForm1" :model="formData" :rules="rules">
              <a-row type="flex" :gutter="12">
                <a-col :span="8">
                  <a-form-model-item ref="leafTypeCode" prop="leafTypeCode" label="">
                    <a-select
                      v-model="formData.leafTypeCode"
                      :disabled="disabled"
                      placeholder=""
                    >
                      <a-select-option
                        v-for="(item, index) in leafTypeList"
                        :key="index"
                        :value="item.leafTypeCode"
                        >{{ item.leafTypeName }}
                      </a-select-option>
                    </a-select>
                  </a-form-model-item>
                </a-col>

                <a-col :span="8">
                  <a-form-model-item label="" prop="orgName" ref="orgName">
                    <a-input-number
                      :min="0"
                      v-model="formData.orgName"
                      placeholder=""
                      :disabled="disabled"
                      autocomplete="off"
                    />
                  </a-form-model-item>
                </a-col>

                <a-col :span="8">
                  <a-form-model-item ref="oprType" prop="oprType" label="">
                    <a-select
                      v-model="formData.oprType"
                      :disabled="disabled"
                      placeholder=""
                      @select="handleChangeOprType"
                    >
                      <a-select-option v-for="item in workOptType" :key="item.id" :value="item.id"
                        >{{ item.name }}
                      </a-select-option>
                    </a-select>
                  </a-form-model-item>
                </a-col>
                <a-col :span="12" layout="horizontal">
                  <a-form-model-item label="">
                    <a-textarea v-model="formData.remark" :max-length="225" />
                  </a-form-model-item>
                </a-col>
              </a-row>
            </ec-form>
          </ec-panel-item>
        </ec-panel>

        <ec-panel color-code="002">
          <ec-panel-header>
            <ec-panel-item-left class="ec-panel-item-left">
              <div class="ec-panel-title">
                <div class="ec-bar bg-color-1"></div>
                <div class="ec-title" style="color: rgb(24, 144, 255); font-weight: 700">
                  /
                </div>
              </div>
            </ec-panel-item-left>
          </ec-panel-header>
          <ec-panel-item>
            <a-alert type="warning">
              <div slot="message" style="color: #1890ff">
                {{ formData.orgName }}
                <span style="color: #1890ff; font-weight: 700">{{
                  resolveData.planTotalWeight
                }}</span>
                {{ weightUnitName }}({{ resolveData.planInnerWeight
                }}{{ weightUnitName }}{{ resolveData.planExportWeight
                }}{{ weightUnitName }}){{ resolveData.planPlantArea }}{{ areaUnitName }}
              </div>
            </a-alert>
          </ec-panel-item>
          <ec-panel-body>
            <ec-table
              border
              show-overflow
              keep-source
              ref="xTable"
              class="my-xtable-antd"
              :loading="saving"
              :data="tableData"
              :edit-config="{
                trigger: 'click',
                mode: 'row',
              }"
            >
              <ec-table-col field="name" title="" align="left" width="150" />
              <ec-table-colgroup title="">
                <ec-table-col
                  field="planTotalWeight"
                  title=""
                  align="center"
                  width="200"
                  :edit-render="{}"
                >
                  <template #edit="{ row }">
                    <a-input-number
                      v-if="row.name === ''"
                      v-model="row.planTotalWeight"
                      :min="0"
                      :max="100000"
                      :precision="2"
                      @blur="handleCurrentDataChange(row, 'planTotalWeight')"
                    ></a-input-number>
                    <template v-else>{{ row.planTotalWeight }}</template>
                  </template>
                </ec-table-col>

                <ec-table-col field="planInnerWeight" title="" align="center" width="150" />
                <ec-table-col
                  field="planExportWeight"
                  title=""
                  width="200"
                  align="center"
                  :edit-render="{}"
                >
                  <template #edit="{ row }">
                    <a-input-number
                      v-if="row.name === ''"
                      v-model="row.planExportWeight"
                      :min="0"
                      :max="100000"
                      :precision="2"
                      @blur="handleCurrentDataChange(row, 'planExportWeight')"
                    ></a-input-number>
                    <template v-else>{{ row.planExportWeight }}</template>
                  </template>
                </ec-table-col>
              </ec-table-colgroup>
              <ec-table-col
                field="planPlantArea"
                title=""
                min-width="150"
                align="right"
                :edit-render="{}"
              >
                <template #edit="{ row }">
                  <a-input-number
                    v-if="row.name === ''"
                    v-model="row.planPlantArea"
                    :min="0"
                    :max="100000"
                    :precision="2"
                    @blur="handleCurrentDataChange(row, 'planPlantArea')"
                  ></a-input-number>
                  <template v-else>{{ row.planPlantArea }}</template>
                </template>
              </ec-table-col>
            </ec-table>
          </ec-panel-body>
        </ec-panel>
      </ec-container-body>
      <ec-container-footer fixed>
        <a-space>
          <a-button type="primary" @click="onSave('save')" :loading="saving"></a-button>
          <a-button @click="onclose"></a-button>
        </a-space>
      </ec-container-footer>
    </ec-container>
  </ec-drawer>
</template>
<script>
import _ from 'lodash';

export default {
  name: 'ExecutionEnterAdjustFormManage',
  data() {
    return {
      title: '',
      isEditor: false,
      disabled: true,
      tableData: [],
      data: {},
      leafTypeName: '',
      leafTypeCode: '',
      weightUnitName: '',
      weightUnit: 1,
      areaUnitName: '',
      areaUnit: 1,
      nowYear: '',
      orgCode: '',
      rowData: '',
      visible: false,
      saving: false,
      isShowSaveAndAdd: true,
      ruleForm: {},
      leafTypeList: [],
      resolveData: {},
      oldObjData: {},
      workOptType: [
        { id: '2', name: '' },
        { id: '1', name: '' },
      ],
      formData: {
        leafTypeCode: '',
        orgName: undefined,
        oprType: undefined,
        remark: undefined,
      },
      beforeAjustData: {},
      rules: {
        oprType: { required: true, message: '', trigger: 'blur' },
        orgName: { required: true, message: '', trigger: 'blur' },
        leafTypeCode: { required: true, message: '', trigger: 'blur' },
      },
    };
  },

  methods: {
    // 
    handleChangeOprType() {
      this.tableData.splice(this.tableData.length - 1, 1);
      this.handleAfterAdjustData();
    },
    // 
    async handleGetBeforeAdjustData() {
      // const beforeData = await this.$apis.bpm.executionAudit.beforeAdjust({
      //   leafTypeCode: this.leafTypeCode,
      //   orgCode: this.orgCode, // todo s
      //   busiYear: this.nowYear,
      // });
      // beforeData.name = '';
      // beforeData.signId = 'before';
      // this.beforeAjustData = beforeData;
      const beforeData = _.cloneDeep(this.oldObjData);
      beforeData.name = '';
      beforeData.signId = 'before';
      this.beforeAjustData = beforeData;

      //  
      this.beforeAjustData = this.handleDataParse(beforeData);
      // 

      const data = {
        leafTypeCode: this.leafTypeCode,
        orgCode: this.orgCode, // todo 
        busiYear: this.nowYear,
      };
      console.log(`${JSON.stringify(data)}`);
      const resolveData = await this.$apis.bpm.executionEnter.getResolveData(data);
      this.resolveData = this.handleResolveDataParse(resolveData);
      console.log(`${JSON.stringify(resolveData)}`);
    },

    //  ()
    handleResolveDataParse(data) {
      const obj = data;
      obj.planTotalWeight = (obj.planTotalWeight / this.weightUnit).toFixed(2);
      obj.planInnerWeight = (obj.planInnerWeight / this.weightUnit).toFixed(2);
      obj.planExportWeight = (obj.planExportWeight / this.weightUnit).toFixed(2);
      obj.planPlantArea = (obj.planPlantArea / this.areaUnit).toFixed(2);
      return obj;
    },

    //  ()
    handleDataParse(data) {
      const obj = data;
      obj.planTotalWeight = obj.baPlanTotalWeight
        ? Number(obj.baPlanTotalWeight / this.weightUnit).toFixed(2)
        : '0.00';
      obj.planInnerWeight = obj.baPlanInnerWeight
        ? Number(obj.baPlanInnerWeight / this.weightUnit).toFixed(2)
        : '0.00';
      obj.planExportWeight = obj.baPlanExportWeight
        ? Number(obj.baPlanExportWeight / this.weightUnit).toFixed(2)
        : '0.00';
      obj.planPlantArea = obj.baPlanPlantArea
        ? Number(obj.baPlanPlantArea / this.areaUnit).toFixed(2)
        : '0.00';
      return obj;
    },

    // 
    handleCurrentDataChange(row, field) {
      const rowObj = row;
      if (field === 'planExportWeight' || field === 'planTotalWeight') {
        const planTotalWeight = Number(row.planTotalWeight) || 0;
        const planExportWeight = Number(row.planExportWeight) || 0;
        if (planTotalWeight < planExportWeight) {
          this.$message.warn('');
          if (field === 'planTotalWeight') {
            rowObj.planTotalWeight = '0';
          }
          if (field === 'planExportWeight') {
            rowObj.planExportWeight = '0';
          }
          const upPlanTotalWeight = Number(rowObj.planTotalWeight) || 0;
          const upPlanExportWeight = Number(rowObj.planExportWeight) || 0;
          const upPlanInnerWeight = (upPlanTotalWeight - upPlanExportWeight).toFixed(2);
          rowObj.planInnerWeight = upPlanInnerWeight;
          return;
        }
        const newPlanInnerWeight = (planTotalWeight - planExportWeight).toFixed(2);
        rowObj.planInnerWeight = newPlanInnerWeight;
      }

      this.tableData.splice(this.tableData.length - 1, 1);
      this.handleAfterAdjustData();
    },

    // 
    async show(row) {
      this.visible = true;
      this.data = row;
      const selectRow = row.selectedRows;
      this.oldObjData = _.cloneDeep(selectRow);

      this.leafTypeName = row.leafTypeObj.leafTypeName;
      this.leafTypeCode = row.leafTypeObj.leafTypeCode;
      this.weightUnitName = row.weightUnitObj.weightUnitName;
      this.weightUnit = row.weightUnitObj.weightUnitCd;
      this.areaUnit = row.areaUnitObj.areaUnitCd;
      this.areaUnitName = row.areaUnitObj.areaUnitName;
      this.orgCode = row.selectedRows.orgCode;
      this.nowYear = row.nowYear;
      this.leafTypeList = row.leafTypeList;

      const { type } = row;

      this.visible = true;
      this.isShowSaveAndAdd = true;
      this.isEditor = false;

      this.formData = {
        leafTypeCode: this.leafTypeCd,
        orgName: undefined,
        oprType: '1',
        remark: undefined,
      };
      // 
      await this.handleGetBeforeAdjustData();
      this.tableData.push(this.beforeAjustData);

      // 
      const currentAdjustData = {
        name: '',
        signId: 'current',
        planTotalWeight: 0,
        planInnerWeight: 0,
        planExportWeight: 0,
        planPlantArea: 0,
      };

      if (type === 'edit') {
        this.isEditor = true;
        this.title = '';
        this.formData = selectRow;
        this.isShowSaveAndAdd = false;
        // 
        currentAdjustData.planTotalWeight = selectRow.planTotalWeight;
        currentAdjustData.planInnerWeight = selectRow.planInnerWeight;
        currentAdjustData.planExportWeight = selectRow.planExportWeight;
        currentAdjustData.planPlantArea = selectRow.planPlantArea;
      }
      this.tableData.push(currentAdjustData);
      console.log(this.tableData);
      // 

      this.handleAfterAdjustData();
    },

    sumNum(list, field) {
      let count = 0;
      const { oprType } = this.formData;
      if (oprType === '1') {
        list.forEach((item) => {
          console.log(`++++++${oprType}`);
          const value = item;
          value[field] = value[field] || 0;
          count += Number(item[field]);
        });
      } else {
        list.forEach((item) => {
          console.log(`------${oprType}`);
          const value = item;
          value[field] = value[field] || 0;
          const { signId } = item;
          if (signId === 'before') {
            count += Number(item[field]);
          } else {
            count -= Number(item[field]);
          }
        });
      }

      return count.toFixed(2);
    },
    // 
    handleAfterAdjustData() {
      const columns = ['planTotalWeight', 'planInnerWeight', 'planExportWeight', 'planPlantArea'];
      const afterAdjustData = {
        name: '',
        signId: 'after',
      };
      columns.forEach((item) => {
        afterAdjustData[item] = this.sumNum(this.tableData, item);
      });
      this.tableData.push(afterAdjustData);
    },

    // 
    handleBeforeSaveAdjustData() {
      const copyTableData = JSON.parse(JSON.stringify(this.tableData));
      const copyOldObjData = JSON.parse(JSON.stringify(this.oldObjData));
      copyOldObjData.remark = this.formData.remark;

      //     
      copyTableData.forEach((item) => {
        const obj = item;
        // 
        if (item.signId === 'before') {
          copyOldObjData.baPlanTotalWeight = (obj.planTotalWeight * this.weightUnit).toFixed(2);
          copyOldObjData.baPlanInnerWeight = (obj.planInnerWeight * this.weightUnit).toFixed(2);
          copyOldObjData.baPlanExportWeight = (obj.planExportWeight * this.weightUnit).toFixed(2);
          copyOldObjData.baPlanPlantArea = (obj.planPlantArea * this.areaUnit).toFixed(2);
        }
        // 
        if (item.signId === 'current') {
          copyOldObjData.planTotalWeight = (obj.planTotalWeight * this.weightUnit).toFixed(2);
          copyOldObjData.planInnerWeight = (obj.planInnerWeight * this.weightUnit).toFixed(2);
          copyOldObjData.planExportWeight = (obj.planExportWeight * this.weightUnit).toFixed(2);
          copyOldObjData.planPlantArea = (obj.planPlantArea * this.areaUnit).toFixed(2);
        }
        // 
        if (item.signId === 'after') {
          copyOldObjData.aaPlanTotalWeight = (obj.planTotalWeight * this.weightUnit).toFixed(2);
          copyOldObjData.aaPlanInnerWeight = (obj.planInnerWeight * this.weightUnit).toFixed(2);
          copyOldObjData.aaPlanExportWeight = (obj.planExportWeight * this.weightUnit).toFixed(2);
          copyOldObjData.aaPlanPlantArea = (obj.planPlantArea * this.areaUnit).toFixed(2);
        }
      });
      console.log(`${JSON.stringify(copyTableData)}`);
      return copyOldObjData;
    },
    // 
    onSave(type) {
      this.$refs.ruleForm1.validate(async (valid) => {
        if (!valid) return;
        this.saving = true;
        const data = this.formData;
        console.log(`${type}===${JSON.stringify(data)}`);
        // 
        const saveData = this.handleBeforeSaveAdjustData();
        this.$apis.bpm.executionEnter
          .updateAdjustData(saveData)
          .then((res) => {
            console.log(JSON.stringify(res));
            if (res.result) {
              this.saving = false;
              this.$message.success('');
              this.$emit('onQuery');
              this.onclose();
            } else {
              this.saving = false;
              this.$message.error('');
            }
          })
          .catch((err) => {
            console.log(err);
            this.saving = false;
          });
      });
    },

    // 
    onclose() {
      this.$refs.ruleForm1.resetFields();
      this.tableData = [];
      this.formData = {};
      this.visible = false;
    },
  },
};
</script>
